body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(248, 166, 15, 0.5);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.component{
  width: 85%;
  margin: 10px auto; 
  text-align: center;
  box-shadow: 10px 10px 2px -5px rgba(107, 97, 97, 0.397);
  background-color: white;


}

.component__topbar{
  height: 50px;
  position: relative;
  bottom: 20px;
  width: 90%;
  left: 5%;
  transform: skew(5deg);
  z-index: 1;
  background-color: rgb(110, 31, 104);
  box-shadow: 10px 10px 8px -10px #888888;
  border-radius: 2px;

}

/* .ticket{
  position: relative;
} */

.popup{
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.5);
  /* width and height for the overlay lightgrey background */
  width: 100%;
  height: 100%;
 
}

.popup__content{
  position: absolute;
  width: 40%;
  height:40vh;
  left: 30%;
  top: 30%;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
}

.close:hover{
  color: cyan
}

.close{
  float: right;
  cursor: pointer;
}

.hidden{
  display: none
}
